html,
body,
#root {
  height: 100%;
  overflow: scroll;
}

:root {
  font-size: 20px;
}

::selection {
  background: #2ddab8;
  color: white;
}

.cb-slideshow,
.cb-slideshow:after { 
    overflow: scroll;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 0; 
}
.cb-slideshow:after { 
    content: '';
    /* background: transparent url(background/blonde.png) repeat top left;  */
}

figure {
  margin: 0 0 0;
}

@keyframes imageAnimation { 
  0% { opacity: 0; animation-timing-function: ease-in; -webkit-animation-timing-function: ease-in; }
  8% { opacity: 1; animation-timing-function: ease-out; -webkit-animation-timing-function: ease-out}
  17% { opacity: 1 }
  25% { opacity: 0 }
  100% { opacity: 0 }
}

.cb-slideshow li  { 
  list-style-type: none;
}

.cb-slideshow li span { 
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  color: transparent;
  background-size: cover;
  background-position: 50% 50%;
  opacity: 0;
  z-index: 0;
  animation: imageAnimation 20s linear infinite 0s; 
}

.cb-slideshow li:nth-child(1) span { 
  background-image: url(background/darker_place.webp);
}
.cb-slideshow li:nth-child(2) span { 
  background-image: url(background/homepage2.jpg);
  animation-delay: 5s;  
}
.cb-slideshow li:nth-child(3) span { 
  background-image: url(background/homepage3.jpg);
  animation-delay: 10s; 
}
.cb-slideshow li:nth-child(4) span { 
  background-image: url(background/homepage4.jpg);
  animation-delay: 15s; 
}

.cb-slideshow li div h3 { 
  /* font-family: 'BebasNeueRegular', 'Arial Narrow', Arial, sans-serif; */
  font-size: 240px;
  padding: 0;
  line-height: 200px; 
}

.no-cssanimations .cb-slideshow li span{
	opacity: 1;
}

